export async function copy(text) {
    try {
        await navigator.clipboard.writeText(text);
        return true;
        // const permissionStatus = await navigator.permissions.query({ name: 'clipboard-write' });
        // if (permissionStatus.state === 'granted') {
        //
        //     return true;
        // }
    } catch (e) {
        console.error('[clipboard] can\'t copy with clipboard.writeText', e);
        return copyOld(text);
    }

    return false;
}

/* based on copy-to-clipboard npm package
https://github.com/sudodoki/copy-to-clipboard/blob/master/index.js
*/
function copyOld(text) {
    let element;
    let successful;
    try {
        element = document.createElement('span');
        element.textContent = text;
        element.style.all = 'unset';
        element.style.position = 'fixed';
        element.style.top = 0;
        element.style.clip = 'rect(0, 0, 0, 0)';
        element.style.whiteSpace = 'pre';
        element.style.webkitUserSelect = 'text';
        element.style.MozUserSelect = 'text';
        element.style.msUserSelect = 'text';
        element.style.userSelect = 'text';

        document.body.appendChild(element);

        element.select();

        successful = document.execCommand('copy');
        if (!successful) {
            throw new Error('unable to copy using document.execCommand');
        }
        return true;
    } catch {
        console.error('unable to copy using document.execCommand', successful);
        try {
            window.clipboardData.setData('text', text);
        } catch (err) {
            console.error('unable to copy using clipboardData ', err);
        }
    } finally {
        if (element) {
            document.body.removeChild(element);
        }
    }

    return false;
}
